import Cates from './components/Cates'
import Clubs from './components/Clubs'
import { parseTime } from '@/utils/time'

export default {
  data () {
    return {
      searchQuery: '',
      clubList: []
    }
  },
  onLoad () {
  },
  components: { Cates, Clubs },
  methods: {
    // 分享
    onShareAppMessage: function (e) {
      var self = this;
      return {
        title: 'RUC社团说---快来找找你的社团啦',
        imageUrl:
          'https://cloud-minapp-31286.cloud.ifanrusercontent.com/1iayhHZQUc1pbeTN.png',
        path: '/pages/finder/index'
      }
    },
    inputBlur (e) {
      this.searchQuery = e.detail.value
    },
    /**
     * 选择分类
     */
    selectCate (name) {
      this.searchQuery = name
      let Club = new qq.BaaS.TableObject('club')
      let query = new qq.BaaS.Query()
      query.contains('cate', name)
      Club.setQuery(query).find().then(res => {
        this.clubList = res.data.objects.map(item => {
          item['time'] = parseTime(item.created_at, '{y}-{m}-{d}')
          return item
        })
      }, err => {
        console.log('err', err)
      })
    },
    /**
    * 搜索框输入内容
    */
    onSearch () {
      let Club = new qq.BaaS.TableObject('club')
      let query = new qq.BaaS.Query()
      query.contains('name', this.searchQuery)
      Club.setQuery(query).find().then(res => {
        this.clubList = res.data.objects.map(item => {
          item['time'] = parseTime(item.created_at, '{y}-{m}-{d}')
          return item
        })
      }, err => {
        console.log('err', err)
      })
    }
  }
}
